"use client";

import Image from "next/image";
import { Product } from "@/lib/data";
import { useCart } from "@/context/CartContext";
import { Button } from "@/components/ui/button";
import { ShoppingCart } from "lucide-react";

interface ProductCardProps {
  product: Product;
  onShowDetails: (product: Product) => void;
  priority?: boolean;
}

export default function ProductCard({ product, onShowDetails, priority = false }: ProductCardProps) {
  const { addToCart } = useCart();

  const handleAddToCart = (e: React.MouseEvent) => {
    e.stopPropagation();
    addToCart(product, 1);
  };

  return (
    <div 
      onClick={() => onShowDetails(product)}
      className="bg-white rounded-lg shadow-sm overflow-hidden border border-gray-100 hover:shadow-md transition-shadow duration-200"
    >
      <div className="relative aspect-square">
        <Image
          src={product.image}
          alt={product.name}
          fill
          className="object-cover"
          sizes="(max-width: 768px) 50vw, 33vw"
          priority={priority}
        />
      </div>
      
      <div className="p-3">
        <h3 className="font-medium text-sm mb-1 line-clamp-2">{product.name}</h3>
        <p className="text-green-600 font-bold">
          Rp {product.price.toLocaleString('id-ID')}
          <span className="text-gray-500 text-xs font-normal">/{product.unit}</span>
        </p>
        
        <div className="mt-2 flex justify-between items-center">
          <span className="text-xs text-gray-500">
            Stok: {product.stock}
          </span>
          <Button
            size="sm"
            variant="outline"
            className="text-green-600 border-green-600 hover:bg-green-50"
            onClick={handleAddToCart}
          >
            <ShoppingCart className="h-4 w-4" />
          </Button>
        </div>
      </div>
    </div>
  );
}
