"use client";

import { useState, useEffect } from "react";
import { products } from "@/lib/data";
import ProductCard from "@/components/ProductCard";
import ProductModal from "@/components/ProductModal";
import { Input } from "@/components/ui/input";
import { Product } from "@/lib/data";
import { Search as SearchIcon } from "lucide-react";

export default function SearchPage() {
  const [searchQuery, setSearchQuery] = useState("");
  const [selectedProduct, setSelectedProduct] = useState<Product | null>(null);
  const [debouncedQuery, setDebouncedQuery] = useState(searchQuery);

  // Debounce search query
  useEffect(() => {
    const timer = setTimeout(() => {
      setDebouncedQuery(searchQuery);
    }, 300);

    return () => clearTimeout(timer);
  }, [searchQuery]);

  // Filter products based on search query
  const filteredProducts = products.filter(product =>
    product.name.toLowerCase().includes(debouncedQuery.toLowerCase()) ||
    product.description.toLowerCase().includes(debouncedQuery.toLowerCase())
  );

  return (
    <div className="container max-w-2xl mx-auto px-4 py-4">
      {/* Search Header */}
      <div className="relative mb-6">
        <SearchIcon className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 h-5 w-5" />
        <Input
          type="search"
          placeholder="Cari sayuran..."
          value={searchQuery}
          onChange={(e) => setSearchQuery(e.target.value)}
          className="pl-10"
          autoFocus
        />
      </div>

      {/* Search Results */}
      {debouncedQuery ? (
        <div className="space-y-4">
          {filteredProducts.length > 0 ? (
            <div className="grid grid-cols-2 gap-3">
              {filteredProducts.map((product) => (
                <ProductCard
                  key={product.id}
                  product={product}
                  onShowDetails={(product) => setSelectedProduct(product)}
                />
              ))}
            </div>
          ) : (
            <div className="text-center py-8">
              <p className="text-gray-500">Tidak ada produk yang ditemukan</p>
            </div>
          )}
        </div>
      ) : (
        <div className="text-center py-8">
          <SearchIcon className="mx-auto h-12 w-12 text-gray-300 mb-4" />
          <p className="text-gray-500">Cari produk yang Anda inginkan</p>
        </div>
      )}

      {/* Product Modal */}
      <ProductModal
        product={selectedProduct}
        isOpen={!!selectedProduct}
        onClose={() => setSelectedProduct(null)}
      />
    </div>
  );
}
